<template>
  <div class="checkbox">
    <input v-model="value" type="checkbox" />
    <label>{{ label }}</label>
  </div>
</template>

<script>
export default {
  props: {
    label: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      value: false
    };
  },
  watch: {
    value(value) {
      this.onChange(value)
    }
  },
  methods: {
    onChange(value) {
      this.$emit('change', value)
    }
  }
}
</script>

